.App {
  font-family: Arial, sans-serif;
}

header {
  background-color: #282c34;
  padding: 1rem;
}

nav ul {
  display: flex;
  list-style: none;
  justify-content: center;
}

nav ul li {
  margin: 0 15px;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 18px;
}

main {
  padding: 20px;
  background-color: #f4f4f4;
}

h1 {
  color: #333;
}

p {
  color: #555;
}

/* Homepage Styles */
.home-container {
  padding: 40px;
}

.post-list {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.post {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.post h3 {
  font-size: 24px;
  margin-bottom: 10px;
}

.post .excerpt {
  font-size: 16px;
  color: #7f8c8d;
}

/* About Page Styles */
.about-container {
  padding: 40px;
}

.about-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Contact Page Styles */
.contact-container {
  padding: 40px;
}

.contact-form {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.contact-form button {
  padding: 10px 20px;
  background-color: #3498db;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.contact-form button:hover {
  background-color: #2980b9;
}

/* Article Page Styles */
.article-container {
  padding: 40px;
}

.article-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
  .header nav ul {
    flex-direction: column;
    margin-top: 20px;
  }

  .header nav ul li {
    margin: 10px 0;
  }

  .post-list {
    gap: 20px;
  }
}